import { CollectionType } from "utils/interfaces";
import { useEffect, useState } from "react";
import dataProvider from "api/dataProvider";

interface RelationFieldValueProps {
  value: string;
  collectionType: CollectionType | undefined;
  labelField: string;
}

const RelationFieldValue: React.FC<RelationFieldValueProps> = ({
  value,
  collectionType,
  labelField,
}) => {
  const [valueLabel, setValueLabel] = useState("");

  useEffect(() => {
    if (collectionType) {
      (async () => {
        try {
          let res = await dataProvider.getDocument(collectionType.id, value);
          if (!res.error) {
            setValueLabel(res[labelField]);
          }
        } catch (er) {
          console.log(er);
        }
      })();
    }
  }, [value, labelField, collectionType]);

  return <>{valueLabel}</>;
};

export default RelationFieldValue;
