import React, { useCallback, memo, useState } from "react";
import Button from "components/Button";
import { useFormik } from "formik";
import Label from "components/Label";
import FieldTypes from "components/Fields";
import { HashedPasswordField } from "components/Fields";
import { useDispatch } from "react-redux";
import classNames from "classnames";
import { AnyField } from "utils/types";
import initialValues from "./Fields/InitialValues";
import { GroundType } from "utils/types";
import { FieldComponentProps } from "./Fields/Components/String";
import memoize from "fast-memoize";

export interface DocumentFormProps {
  onSubmit: (a: { [key: string]: any } | null) => void;
  fields: AnyField[];
  editableDocument?: { [key: string]: any };
  docId?: string;
  ground: GroundType;
  level: number;
  singleName?: string;
}

const DocumentForm: React.FC<DocumentFormProps> = ({
  onSubmit,
  fields,
  editableDocument,
  level,
  docId,
  ground,
  singleName,
}) => {
  const [scrollTop, setScrollTop] = useState(0);

  const allFieldTypes = [...FieldTypes, HashedPasswordField];

  const dispatch = useDispatch();

  const initial = fields.reduce(
    (a, b) => ({
      ...a,
      [b.id]:
        editableDocument && editableDocument[b.id]
          ? editableDocument[b.id]
          : initialValues[b.type](b),
    }),
    {}
  );

  const { values, errors, setFieldValue, handleSubmit, submitCount, isSubmitting } = useFormik({
    validateOnChange: false,
    initialValues: initial as { [index: string]: any },
    enableReinitialize: true,
    onSubmit: async (vals) => {
      await dispatch({ type: "SET_DOCUMENT_CHANGED", payload: false });
      await onSubmit(vals);
    },
    validate: (vals) => {
      const errors: any = {};
      fields.forEach((x) => {
        let fieldType = allFieldTypes.find((f) => f.type === x.type);
        if (!fieldType) return errors;
        let error = fieldType.validate(vals[x.id]);
        if (error) {
          errors[x.id] = error;
        }
      });

      return errors;
    },
  });

  const hasChanged = !editableDocument || JSON.stringify(initial) !== JSON.stringify(values);

  React.useEffect(() => {
    dispatch({ type: "SET_DOCUMENT_CHANGED", payload: hasChanged });
  }, [hasChanged, dispatch]);

  const onChange = useCallback(
    memoize((id: string) => (newValue: any) => {
      setFieldValue(id, newValue);
    }),
    [setFieldValue]
  );

  const components: { [key in keyof typeof initialValues]: React.FC<FieldComponentProps<any>> } =
    FieldTypes.reduce((a, b) => ({ ...a, [b.type]: b.Component }), {} as any);

  return (
    <div className="h-full flex flex-col w-full">
      <div
        className={classNames(
          { "shadow-md": scrollTop },
          "flex justify-between transition-all flex-wrap mb-2 -mx-7 -mt-7 px-7 pt-7"
        )}
      >
        <div>
          <div
            className={classNames(
              "font-medium text-27px leading-none capitalize mb-1 mr-4",
              `${ground.toLowerCase()}TextColor`
            )}
          >
            {docId ? `Edit Document` : singleName ? singleName : `Add Document`}
          </div>
          {!docId ? null : (
            <div className={classNames(`${ground.toLowerCase()}TextColor`, "mb-3 text-xs")}>
              Doc id: {docId}
            </div>
          )}
        </div>
        <Button
          type="submit"
          onClick={() => handleSubmit()}
          disabled={isSubmitting || !hasChanged}
          className={`h-34px ${
            hasChanged
              ? "primaryBackground buttonPrimaryTextColor"
              : "buttonDisabledBackground buttonDisabledTextColor cursor-default"
          }  mb-2`}
        >
          {isSubmitting
            ? "Loading..."
            : editableDocument != null || singleName
            ? "Save"
            : "Publish"}
        </Button>
      </div>
      {!Object.keys(values).length ? null : (
        <div
          className="flex-grow h-0 overflow-y-auto overflow-x-hidden pt-4 -mx-7 -mb-7 px-7"
          onScroll={(e) => {
            e.persist();
            setScrollTop(e.currentTarget.scrollTop);
          }}
        >
          <div className="flex flex-wrap -mx-3">
            {fields
              .filter((x) => !x.isDefault)
              .map((x, i) => {
                const Component = components[x.type];

                return (
                  <div
                    key={`field-${i}`}
                    className={`${
                      ["rich-text", "collection"].includes(x.type) ? "w-full" : "sm:w-1/2 w-full"
                    } px-3 mb-12`}
                  >
                    <Label
                      ground={ground}
                      className="mb-2 font-medium"
                      error={submitCount > 0 ? errors[x.id]?.toString() : null}
                    >
                      {x.id}{" "}
                    </Label>
                    {!Component ? null : (
                      <Component
                        onValue={onChange(x.id)}
                        ground={ground}
                        value={values[x.id]}
                        name={x.id}
                        level={level}
                        field={x as never}
                      ></Component>
                    )}
                  </div>
                );
              })}
          </div>
        </div>
      )}
    </div>
  );
};

export default DocumentForm;
